
.logo{
  height: 80px;
  line-height: 80px;
  background-color: #5CACEE;
}
.logo>h1{
  display: inline-block;
  color: #fff;
  margin-left: 50px;
  cursor: pointer;
}
.login_area{
  width: 450px;
  height: 300px;
  border: 10px solid #dddddd;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -150px;
  border-radius: 10px;
  text-align: center;
  background-color: #2c3e50;
}
.form-group{
  margin: 40px 10px;
}
.form-group label{
  display: inline-block;
  padding: 4px 12px;
  font-size: 14px;
  width: 20%;
  border: 1px solid #5cacee;
  background-color: #5cacee;
  color: #ffffff;
  border-radius: 6px;
  text-align: center;
}
.form-group input{
  width: 40%;
  margin-right: 40px;
  border: 1px solid #2c3e50;
  line-height: 28px;
  border-radius: 6px;
  font-size: 14px;
}
.login_area button{
  margin: 0px 10px;
}
input[class=submit]{
  padding: 6px 14px;
  background-color: #39f;
  color: #ffffff;
  border-radius: 4px;
  border: 1px solid #39f;
  cursor: pointer;
}



