
#header-top>.nav{
  overflow: hidden;
  border-bottom: 1px solid #ddd;
}
#header-top>.nav>.active>a{
  color: #5cacee;
  border-bottom: 2px solid #5cacee;
}
#header-top>.nav>li{
  float: left;
  height: 60px;
  line-height: 60px;
}
#header-top>.nav>li>a{
  display: inline-block;
  font-size: 14px;
  line-height: 60px;
  height: 60px;
  padding: 0px 20px;
  color: #2c3e50;
}
