/*}*/
#header-top>.logo{
  background-color: #5CACEE;
  height: 80px;
  line-height: 80px;
}
#header-top>.logo>h1{
  display: inline-block;
  color: #fff;
  margin-left: 50px;
  cursor: pointer;
}
#header-top>.logo>.changeIconA{
  display: inline-block;
  float: right;
  color: #fff;
  margin: 0px 5px;
}
#header-top>.logo>.changeIcon{
  margin-right: 50px;
}



